import { useState } from "react";
import { useDispatch } from "react-redux";
import { signup, login } from "../../store/actions/userActions";
import "./SignupPage.scss";

export const SignupPage = (props) => {
  const [userCred, setUserCred] = useState(null);
  const dispatch = useDispatch();

  const handleChange = ({ target }) => {
    const field = target.name;
    const value = target.value === "number" ? +target.value : target.value;
    setUserCred({ ...userCred, [field]: value });
  };

  const onSignUp = (ev) => {
    ev.preventDefault();
    dispatch(signup(userCred));
    props.history.push("/");
  };

  const onLogin = (ev) => {
    ev.preventDefault();
    dispatch(login(userCred));
    props.history.push("/");
  };

  return (
    <div className="signup-page">
      <h1>Signup</h1>
      <form onSubmit={onSignUp}>
        <label htmlFor="fullname">Full name</label>
        <input
          required
          type="text"
          id="fullname"
          onChange={handleChange}
          name="fullname"
        />

        <label htmlFor="username">User name</label>
        <input
          required
          type="text"
          id="username"
          onChange={handleChange}
          name="username"
        />

        <label htmlFor="password">Password</label>
        <input
          required
          type="password"
          id="password"
          onChange={handleChange}
          name="password"
        />
        <label htmlFor="email">Email</label>
        <input
          required
          type="text"
          id="email"
          onChange={handleChange}
          name="email"
        />
        <label htmlFor="phone">Phone number</label>
        <input
          required
          type="text"
          id="phone"
          onChange={handleChange}
          name="phone"
        />
        <button>Sign up</button>
        <hr />
      </form>

      <h1>Login</h1>
      <form onSubmit={onLogin}>
        <label htmlFor="username">User name</label>
        <input
          required
          type="text"
          id="username"
          onChange={handleChange}
          name="username"
        />

        <label htmlFor="password">Password</label>
        <input
          required
          type="password"
          id="password"
          onChange={handleChange}
          name="password"
        />
        <button>Login</button>
      </form>
    </div>
  );
};
