import { NavLink } from "react-router-dom";
import { useHistory } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../../store/actions/userActions";
import bitcoinLogo from "../../assets/icons/bitcoin.png";
import "./AppHeader.scss";

export const AppHeader = () => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const history = useHistory();
  const dispatch = useDispatch();
  const onGoHome = () => history.push("/");

  const onLogout = () => {
    dispatch(logout());
  };

  return (
    <div className="app-header">
      <div onClick={onGoHome} className="app-logo">
        <img src={bitcoinLogo} alt="" />
      </div>
      <ul>
        <li>
          <NavLink exact to="/" activeClassName="active-nav">
            Home
          </NavLink>
        </li>
        <li>
          <NavLink to="/contact" activeClassName="active-nav">
            Contacts
          </NavLink>
        </li>
        <li>
          <NavLink to="/statistic" activeClassName="active-nav">
            Statistics
          </NavLink>
        </li>
        <li>
          {!loggedUser ? (
            <NavLink to="/signup" activeClassName="active-nav">
              SignUp
            </NavLink>
          ) : (
            <NavLink to="/signup" activeClassName="active-nav" onClick={onLogout}>
              Logout
            </NavLink>
          )}
        </li>
      </ul>
    </div>
  );
};
