import { useEffect, useState } from "react";
import { Chart } from "../../cmps/Chart/Chart";
import { bitcoinService } from "../../services/bitcoinService";
import "./StatisticPage.scss";

export const StatisticPage = () => {
  const [marketPrice, setMarketPrice] = useState(null);
  const [marketPriceValues, getMarketPriceValues] = useState(null);

  useEffect(() => {
    const loadMarketPrice = async () => {
      try {
        const marketPrice = await bitcoinService.getMarketPrice();
        setMarketPrice(marketPrice);
        const values = marketPrice.values.map((value) => value.y);
        getMarketPriceValues(values);
      } catch (err) {
        console.log(err);
      }
    };
    loadMarketPrice();
  }, []);

  return (
    marketPrice &&
    marketPriceValues && (
      <div>
        <h1>Statistic page:</h1>
        <Chart
          title={marketPrice.name}
          description={marketPrice.description}
          data={marketPriceValues}
          color={"red"}
        />
      </div>
    )
  );
};
