import { useState } from "react";
import { useHistory } from "react-router";
import "./TransferFund.scss";

export const TransferFund = ({ contact, onTransferCoins, match }) => {
  const [transferAmount, setTransferAmount] = useState(0);
  const history = useHistory();

  const transferCoins = (ev) => {
    onTransferCoins(transferAmount, ev);
    history.push("/contact");
  };

  const handleChange = ({ target }) => {
    const value = target.type === "number" ? +target.value : target.value;
    setTransferAmount(value);
  };

  return (
    <section className="transfer-fund">
      <h3>Transfer coins to {contact.name}:</h3>
      <form onSubmit={transferCoins}>
        <label htmlFor="amount">Amount:</label>
        <input required type="number" id="amount" onChange={handleChange} />
        <button>Transfer</button>
      </form>
    </section>
  );
};
