import { contactService } from "../../services/contactService";

export const getContacts = (filterBy) => {
  return async (dispatch) => {
    const contacts = await contactService.getContacts(filterBy);
    dispatch({ type: "SET_CONTACTS", contacts });
  };
};

export const getContactById = (contactId) => {
  return async (dispatch) => {
    const contact = await contactService.getContactById(contactId);
    dispatch({ type: "SET_CONTACT", contact });
  };
};
export const addContact = (username) => {
  return async (dispatch) => {
    const contact = await contactService.addContact(username);
    dispatch({ type: "SET_CONTACT", contact });
  };
};

export const saveContact = (contact) => {
  return async (dispatch) => {
    const isAdd = !contact._id;
    const updatedContact = await contactService.saveContact(contact);
    if (isAdd) dispatch({ type: "ADD_CONTACT", contact: updatedContact });
    else dispatch({ type: "UPDATE_CONTACT", contact: updatedContact });
  };
};

export const removeContact = (contactId) => {
  return async (dispatch) => {
    await contactService.removeContact(contactId);
    dispatch({ type: "REMOVE_CONTACT", contactId });
  };
};
