import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { MoveList } from "../../cmps/MoveList/MoveList";
import { TransferFund } from "../../cmps/TransferFund/TransferFund";
import { getContactById } from "../../store/actions/contactActions";
import {
  setLoggedInUser,
  transferCoins,
} from "../../store/actions/userActions";

import "./ContactDetailsPage.scss";

export const ContactDetailsPage = (props) => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const contact = useSelector((state) => state.contactReducer.currContact);
  const [userToContactMoves, setUserToContactMoves] = useState([]);
  const dispatch = useDispatch();

  useEffect(() => {
    const contactMoves = loggedUser?.moves?.filter((move) => {
      return move.toId === props.match.params.id;
    });

    if (loggedUser) {
      dispatch(getContactById(props.match.params.id));
    } else {
      dispatch(setLoggedInUser());
    }
    if (contactMoves) setUserToContactMoves(contactMoves);
  }, [props.match.params.id]);

  const onGoContactPage = () => {
    props.history.push("/contact");
  };

  const onEditContact = () => {
    props.history.push(`/contact/edit/${contact._id}`);
  };

  const onTransferCoins = (transferAmount, ev) => {
    ev.preventDefault();
    if (loggedUser.coins < transferAmount) return alert("No enough money");
    dispatch(transferCoins(contact._id, transferAmount));
  };

  if (!contact) return <div>Contact not found...</div>;
  return (
    <div className="contact-details-page">
      <h1>Contact Details Page:</h1>
      <img src={`https://robohash.org/${contact._id}`} alt="" />
      <p>{contact._id}</p>
      <p>{contact.name}</p>
      <p>{contact.email}</p>
      <p>{contact.phone}</p>
      <TransferFund contact={contact} onTransferCoins={onTransferCoins} />
      {userToContactMoves && <MoveList transfers={userToContactMoves} />}
      <button onClick={onGoContactPage}>Back</button>
      {/* <button onClick={onEditContact}>Edit</button> */}
    </div>
  );
};
