import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ContactFilter } from "../../cmps/ContactFilter/ContactFilter";
import { ContactList } from "../../cmps/ContactList/ContactList";
import { getContacts, addContact } from "../../store/actions/contactActions";
import { setLoggedInUser } from "../../store/actions/userActions";
import "./ContactPage.scss";

export const ContactPage = ({ history }) => {
  const [filterBy, setFilterBy] = useState(null);
  const [contactName, setContactName] = useState(null);
  const loggedUser = useSelector((state) => state.userReducer.user);
  const contacts = useSelector((state) => state.contactReducer.contacts);
  const contact = useSelector((state) => state.contactReducer.currContact);
  const dispatch = useDispatch();

  const changeFilter = (filterBy) => {
    setFilterBy(filterBy);
  };
  const onAddContact = (ev) => {
    ev.preventDefault();
    dispatch(addContact(contactName));
  };

  const handleChange = ({ target }) => {
    setContactName(target.value);
  };

  useEffect(() => {
    if (loggedUser) {
      dispatch(getContacts(filterBy));
    } else {
      dispatch(setLoggedInUser());
    }
    //  else {
    //   alert("Please login");
    //   history.push("/signup");
    // }
  }, [ contact]);

  return (
    loggedUser && (
      <div className="contact-page">
        <form onSubmit={onAddContact}>
          <label htmlFor="contactName">Search contact to add</label>
          <input
            type="text"
            id="contactName"
            onChange={handleChange}
            name="contactName"
          />
          <button>Add contact</button>
        </form>

        <ContactFilter onChangeFilter={changeFilter} />
        {contacts ? (
          <ContactList contacts={contacts} />
        ) : (
          <h1>No contacts to show</h1>
        )}
      </div>
    )
  );
};
